async function getAnime(id: string) {
  const res = await fetch(`https://api.jikan.moe/v4/anime/${id}`, {
    cache: "no-store",
  });

  if (!res.ok) return null;
  const data = await res.json();
  return data.data;
}

export default async function AnimeDetail({ params }: { params: { id: string } }) {
  const anime = await getAnime(params.id);

  if (!anime) {
    return (
      <div className="text-center py-20">
        <h2 className="text-3xl font-bold">Anime not found</h2>
        <p className="opacity-60 mt-2">ID: {params.id}</p>
      </div>
    );
  }

  return (
    <div className="flex flex-col md:flex-row gap-8">
      {/* Poster */}
      <img
        src={anime.images.jpg.large_image_url}
        alt={anime.title}
        className="w-64 rounded-xl shadow-lg"
      />

      <div className="flex-1">
        {/* Title */}
        <h1 className="text-4xl font-bold">{anime.title}</h1>
        <p className="text-lg opacity-70 mb-3">{anime.title_japanese}</p>

        {/* Info Grid */}
        <div className="grid grid-cols-2 gap-4 text-sm mb-6">
          <p><span className="opacity-60">Type:</span> {anime.type}</p>
          <p><span className="opacity-60">Status:</span> {anime.status}</p>
          <p><span className="opacity-60">Episodes:</span> {anime.episodes || "?"}</p>
          <p><span className="opacity-60">Score:</span> {anime.score || "N/A"}</p>
          <p><span className="opacity-60">Year:</span> {anime.year || "?"}</p>
          <p>
            <span className="opacity-60">Genres:</span>{" "}
            {anime.genres.map((g: any) => g.name).join(", ")}
          </p>
        </div>

        {/* Synopsis */}
        <h2 className="text-2xl font-bold mb-2">Synopsis</h2>
        <p className="opacity-80 leading-relaxed">{anime.synopsis}</p>

        {/* Watch Button */}
        <a
          href={`/watch/${params.id}`}
          className="mt-6 inline-block bg-blue-600 px-5 py-2 rounded-lg hover:bg-blue-700 transition"
        >
          Watch Now
        </a>
      </div>
    </div>
  );
}